"use client";

import React, { useCallback, useMemo, useState } from "react";
import { useRouter } from "next/navigation";
import {
  ZusText,
  ZusButton,
  ZusInput,
  ZusSelect,
} from "@/components/zus-ui";
import { PueLoginPanel } from "@/components/pue/pue-panel";

const currentYear = new Date().getFullYear();
const YEARS = Array.from({ length: 70 }, (_, i) => currentYear - i).reverse();
const RETIRE_YEARS = Array.from({ length: 60 }, (_, i) => currentYear + i);

function fmtPLN(n: number) {
  return new Intl.NumberFormat("pl-PL", {
    style: "currency",
    currency: "PLN",
    maximumFractionDigits: 0,
  }).format(isFinite(n) ? n : 0);
}

export default function SignUpFormPage() {
  const router = useRouter();

  // --- form state
  const [age, setAge] = useState<number>(30);
  const [sex, setSex] = useState<string>("M");
  const [salary, setSalary] = useState<number>(9000);
  const [startYear, setStartYear] = useState<number>(currentYear - 6);
  const [retireYear, setRetireYear] = useState<number>(currentYear + 35);

  // Salary: keep arrows stepping by 500 (typing freeform still allowed)
  const onSalaryKeyDown = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "ArrowUp" || e.key === "ArrowDown") {
      e.preventDefault();
      const delta = e.key === "ArrowUp" ? 500 : -500;
      setSalary((v) => Math.max(0, Math.round((v + delta) / 500) * 500));
    }
  }, []);
  const onSalaryChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const raw = Number(e.target.value.replace(/\s/g, "").replace(",", "."));
    setSalary(Number.isFinite(raw) ? raw : 0);
  }, []);

  const isValid = useMemo(() => {
    return (
      Number.isFinite(age) &&
      age >= 16 &&
      (sex === "F" || sex === "M") &&
      Number.isFinite(salary) &&
      salary >= 0 &&
      Number.isFinite(startYear) &&
      Number.isFinite(retireYear) &&
      retireYear > startYear
    );
  }, [age, sex, salary, startYear, retireYear]);

  function onSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!isValid) return;
    const q = new URLSearchParams({
      age: String(age),
      sex,
      grossSalary: String(Math.round(salary)),
      workStartYear: String(startYear),
      retireYear: String(retireYear),
    });
    router.push(`/simulator?${q.toString()}`);
  }

  return (
    <form onSubmit={onSubmit} noValidate className="bg-white rounded-2xl">
      <div className="grid grid-cols-1 md:grid-cols-2">
        {/* LEFT: Demographics */}
        <div className="p-6 md:p-8">
          <div className="flex flex-col">
            <h3 className="zus-text-h2 text-zus-green">Twoje dane</h3>

            <ZusText className="mt-2 text-neutral-700">
              Wypełnij podstawowe informacje demograficzne. Te dane pomogą nam lepiej dopasować
              prognozę i porównania kontekstowe.
            </ZusText>
          </div>

          <div className="p-0 mt-6 space-y-5">
            {/* Age */}
            <ZusInput
              id="age"
              label="Wiek"
              type="number"
              min={16}
              max={80}
              step={1}
              value={age}
              onChange={(e) => setAge(parseInt(e.target.value || "0", 10))}
              className="w-40"
              required
            />
            <p className="zus-text-small mt-1 text-neutral-600">Minimalny wiek: 16 lat.</p>

            {/* Sex (picker left as-is for now) */}
            <div>
              <label htmlFor="sex" className="zus-label">Płeć</label>
              {/* Keep your existing picker component for now */}
              {/* eslint-disable-next-line @typescript-eslint/ban-ts-comment */}
              {/* @ts-ignore */}
              <ZusSelect
                options={[
                  { value: "F", label: "Kobieta" },
                  { value: "M", label: "Mężczyzna" },
                ]}
                value={sex}
                onChange={setSex}
                className="w-52 mt-2"
              />
            </div>

            {/* Salary */}
            <div>
              <ZusInput
                id="salary"
                label="Pensja brutto (PLN / m-c)"
                type="number"
                min={0}
                step={500}
                value={Number.isFinite(salary) ? salary : 0}
                onChange={onSalaryChange}
                onKeyDown={onSalaryKeyDown}
                className="w-56"
                aria-describedby="salary-help"
                required
              />
              <div className="mt-2 flex items-center gap-3">
                <span className="zus-badge zus-badge-neutral">{fmtPLN(salary || 0)}</span>
              </div>
              <p id="salary-help" className="zus-text-small mt-1 text-neutral-600">
                Strzałki ↑/↓ zmieniają kwotę o 500.
              </p>
            </div>

            {/* Work start year (picker left as native select for now) */}
            <div>
              <label htmlFor="startYear" className="zus-label">Rok rozpoczęcia pracy</label>
              <select
                id="startYear"
                value={startYear}
                onChange={(e) => setStartYear(parseInt(e.target.value, 10))}
                className="zus-select w-48 mt-2"
              >
                {YEARS.map((y) => (
                  <option key={y} value={y}>{y}</option>
                ))}
              </select>
            </div>

            {/* Planned retirement year (picker left as native select for now) */}
            <div>
              <label htmlFor="retireYear" className="zus-label">Planowany rok przejścia na emeryturę</label>
              <select
                id="retireYear"
                value={retireYear}
                onChange={(e) => setRetireYear(parseInt(e.target.value, 10))}
                className="zus-select w-56 mt-2"
              >
                {RETIRE_YEARS.map((y) => (
                  <option key={y} value={y}>{y}</option>
                ))}
              </select>
              <p className="zus-text-small mt-1 text-neutral-600">
                Musi być później niż rok rozpoczęcia pracy.
              </p>
            </div>
          </div>

          <ZusButton
            variant="primary"
            type="submit"
            className="mt-4"
            disabled={!isValid}
            aria-disabled={!isValid}
          >
            Kontynuuj
          </ZusButton>
        </div>

        {/* RIGHT: PUE/eZUS login panel */}
        <PueLoginPanel />
      </div>
    </form>
  );
}